.contact svg{
    display: block;
}
.uppercont{
    display: flex;
}
.img{
    width:100%;
    height:100%
}
.imgDiv{
    width: 45%;
    min-height: 400px;
}
#contact{
    width: 85%;
    padding: 20px 40px 100px 40px ; 
    margin: auto;   
}
.contact-github{
    color: var(--yellow)
}
.contactmain{ 
    text-align: center;
}
form{
    width: 100%;
}
.formContent{
    padding: 20px;
}
.content{
    display: flex;
    justify-content: space-around;
}
form{
    margin-top: 30px;
}
.content input {
    background: transparent;
    position: sticky;
    padding: 23px;
    margin: 10px;
    margin-top: 20px;
    border-radius: 20px;
    font-size: 15px;
    border: 0px;
    border-bottom: 2px solid var(--yellow);
    /* padding: .25rem .5rem .5rem 0;   */
    width:30%;    
}
.content input:active{
    border: 0px;
}
.header{
    font-family: 'Signika Negative', sans-serif;
    color: palevioletred; 
    letter-spacing: 7px;
    font-size: 65px;
    margin-bottom: 10px;
}
