    color: #977dc7;

    font-size: 20px;
    letter-spacing: 3px;
} */

.contactdetail{
    font-size: 25px;
    text-decoration: none;
    letter-spacing: 3px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: palevioletred; 
    font-weight: 750;
}
.btton{
    border-radius:34px;
    border:none;
    color:rgb(7, 99, 5);
    position: sticky;
    font-size: 11px;
    font-weight: 800;
    box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
    padding:11px 26px 11px 26px;
    background: linear-gradient(100deg, #fdc50f 26.71%, #fb982f 99.36%);
  }
  .btton:hover {
    background: white;
    cursor: pointer;
    border: 1px solid var(--orange);
    color: var(--orange);
  }






.contactWrapper {
    /* display: flex; */
    height: 80vh;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.contactIconWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    color: white;
    padding: 30px 0px;
}

.contactIconWrapper a{
    color: var(--white);
    border-color: var(--yellow);
    text-align: center;
    margin: 0.5em;
}
.contactIcons {
    height: 7vh;
    width: auto;
    width: 30%;
    font-size: 20px;
    cursor: pointer;
    margin-top: -40px;  
}
.contactsi{
    margin-top: 10px;
}
.fonts{
    font-size: 30px;
  
}

/* .contactIcons:hover {
     color: palevioletred; 
}  */

 @media screen and (max-width: 540px) {
    .contactHeading {
        margin: 0 2em;
        text-align: center;
    }
    .header{
        font-size: 55px
    }

    .contactIcons {
        height: 7vh;
        width: auto;
        width: 30%;
        font-size: 15px;
        cursor: pointer;
        margin-top: -40px;  
    }
    .contactdetail{
        font-size: 21px;  
    }
   
}  






