#nav-menu{
    position: fixed;
    top: -1;  
}
.wrap{
  width: 96%;
  display:flex;
  padding: 0 30px;
  z-index: 10;
  justify-content: space-around;
}
.main-leftdiv{
display:flex;
flex:1;
gap:1.5rem;
align-items:center;
font-weight: bold;
 letter-spacing:3px;
 margin-left: 20px;
}
.left-name{
    font-size:1.5rem;
    font-weight:bolder; 
}
.main-.right-list{
    display:flex;
    /* flex:6; */
    align-items:center;
    font-weight: 400;
}
.right-list{
    flex:9;
}

.right-list>ul{
    display:flex;
    text-decoration: none;
    margin-right: 4rem;
    transition: .5s;
    /* justify-content: space-around; */
    gap:3rem;
    letter-spacing:2.5px;
    font-weight: bold;
    list-style: none;
    padding: 2px;
}

.right-list>ul li:hover{
cursor:pointer;
padding: 3px;
color:var(--yellow);
transition: .5s;
}
.nav-butt{
    letter-spacing:3px;
}
.nav-butt{
   padding:10px;
   height: 75%;
   align-items: center;
   margin-top: 10px;
   transition: .5s;
  }
  .nav-link{
    text-decoration: none;
  }
  .newLink{
    text-decoration: none;
  }

  @media screen and (min-width: 750px) and (max-width: 1100px) {
    .right-list>ul{
        gap:1rem; 
    }
    .wrap{
        display:flex;
        height:10vh;
        z-index: 10;
        justify-content: space-between; 
        width: 96%;
        margin: auto;
      border-bottom: 2px solid var(--yellow);
    }
    .nav-butt{
        letter-spacing:.5px;
        padding:5px;
        background-color: white;
        transition: .5s;
    }
    .right-list>ul li:hover{
        cursor:pointer;
        padding: 3px;
        transition: .5s;
        color:var(--yellow);
        }
        .right-list>ul{
            margin-right: 0;
            gap:1;
            font-size:0.9rem;
        }
        .main-leftdiv{
            display:flex;
            flex:1;
            gap:1rem;
             letter-spacing:1px;
            }
            .left-name{
                font-size:0.9rem;
                font-weight:bolder;
            }
    
  }

  @media screen and  (max-width: 752px){
    .left-name{
        font-size:1.2rem;
        font-weight:bolder;
    }
    .nav-butt{
      font-weight: 10px;
    }
 .dropBox{
  text-align: center;
  position: fixed;
  opacity: .9;
  background-color: var(--yellow);
  width:50%;
  z-index: 10;
  float: left;
  margin-top: 60px;
  transition: .4s;
  border-radius: 15px;
 }
 .dropBox ul{
    list-style:none;
   }
   .dropBox ul li{
   margin-top: 30%;
   font-size: 25px;
    }
.modes{
  margin-left: -5px;
}
    .dropin{
        left:-400%;
         position: absolute;
    }
    .wrap{
      width: 96%;
      padding:0 20px;
      margin-left: 0;
    }
    .alterbar{
 margin: 10px 15px 10px 10px;
    }

  }