.App {
  font-family: 'PT Sans', sans-serif;
  overflow: hidden;
  color:var(--black);
  opacity:1; 
}
:root{
 --yellow:#f5c71a;
 --black:#263159;
 --gray:#65647C;
 --purple:#F3CCFF;
 --orange:#FFD700;
 --lightgreen:#9ED5C5;
 --boxShadow:0px 23px 30px rgb(0 0 0 / 13%);
 --smboxshadow:-69px 41px 50px rgba(0,0,0,0.05);
}

*{
  scroll-behavior: smooth;
}
 a{
  position: sticky;
}

.button{
  border-radius:34px;
  border:none;
  color:white;
  position: sticky;
  font-size: 11px;
  font-weight: 800;
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
  padding:11px 26px 11px 26px;
  background: linear-gradient(100deg, #fdc50f 26.71%, #fb982f 99.36%);
}
.button:hover {
  background: white;
  cursor: pointer;
  border: 1px solid var(--orange);
  color: var(--orange);
}



.area{
  width: 100%;
  height: 100%;
  position:fixed;
  top: 0;
  opacity: 3;
}

.circles{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -10;
}
.circles li{
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(223, 210, 32, 0.2);
  animation: animate 25s linear infinite;
  bottom: -150px;
  z-index: -10;
}

.circles li:nth-child(1){
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
  z-index: -10;
}
.circles li:nth-child(2){
  left: 5%;
  width: 40px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
  z-index: -10;
}

.circles li:nth-child(3){
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
  z-index: -10;
}

.circles li:nth-child(4){
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
  z-index: -10;
}

.circles li:nth-child(5){
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
  z-index: -10;
}

.circles li:nth-child(6){
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
  z-index: -10;
}

.circles li:nth-child(7){
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
  z-index: -10;
}

.circles li:nth-child(8){
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
  z-index: -10;
}

.circles li:nth-child(9){
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
  z-index: -10;
}

.circles li:nth-child(10){
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
  z-index: -10;
}
@keyframes animate {

  0%{
      transform: translateY(0) rotate(0deg);
      opacity: 1;
      border-radius: 0;
  }

  100%{
      transform: translateY(-1000px) rotate(720deg);
      opacity: 0;
      border-radius: 50%;
  }

}























