
.section{
    width: 80%;
    margin:auto;
    display: flex;
    position: sticky;
    box-shadow: #fdc50f 0px 0px 0px 0px, #fb982f 4px 10px 5px 0px;
    min-height: 30vh;
}
.heading{
    font-family: 'Signika Negative', sans-serif;
    color: palevioletred; 
    letter-spacing: 3px;
}
.gif{
    width:100%;
    margin: auto;
    margin-top: 30px;
}
.gifAll{
    width:28%;
    margin: auto;
}
.aboutsec{
    width: 60%;
    margin-top: 40px;
    padding: 30px;
    margin-top: 30px;
}
.detail{
    font-size: 20px;
    margin-right: 20px;
    margin-top: 40px;
    font-weight: 600;
}

.smalldiv{
    /* margin-top: 30px; */
    display: flex;
    gap: 10px;
    justify-content: space-between;
}


   
.detalsmallDiv{
    padding: 10px;
    border-radius: 40px;
    box-shadow: #fdc50f 0px 1px 2px 0px, #fb982f 0px 10px 3px 1px;
   
}

@media screen and (min-width: 750px) and (max-width: 935px) {
    .detail{
        font-size: 15px;
    }
    .smalldiv{
        display: flex;
        gap: 10px;
        justify-content: space-between;
        font-size: 13px;
        padding: 40px;
    }
    .detalsmallDiv{
        padding: 5px;
        text-align: center;
        border-radius: 50px;
        box-shadow: rgba(253, 229, 14, 0.25) 0px 54px 55px, rgba(11, 8, 8, 0.12) 0px -12px 30px, rgba(250, 223, 45, 0.12) 0px 4px 6px, rgba(242, 228, 27, 0.17) 0px 12px 13px, rgba(238, 209, 22, 0.09) 0px -3px 5px;
    }
    .gifAll{
        width:48%;
        margin: auto;
    }
    .section{
    margin-top: 30px;
    }
}

@media screen and (min-width: 890px){
    .section{
        padding: 50px;
    }
    .detalsmallDiv{
        margin-top: 20px;
    }
}
@media screen and (max-width: 749px){
    #about{
        width: 80%;
        margin:auto;
        display: block;
       margin-top: 80px;
    }
    .gif{
        margin: auto;
        margin-top: 10px;
        border-radius: 50px;
    }
    .gifAll{
        width:90%;
        margin: auto;
    }
    .aboutsec{
        width: 90%;
        margin: auto;
        text-align:start;
        padding: 30px;
    }
    .detail{
        font-size: 17px;
        font-weight: 700;
    }
    .headingnow{
        font-family: 'Signika Negative', sans-serif;
        text-align: center;
        color: palevioletred; 
    }
    .smalldiv{
        margin-top: 20px;
        display: flex;
        gap: 10px;
        justify-content: space-between;
    }
    
    .detalsmallDiv{
      
        padding: 10px;
        border-radius: 40px;
        box-shadow: rgba(245, 226, 12, 0.05) 0px  0px 1px;
        box-shadow: rgba(242, 222, 5, 0.05) 0px 1px 2px 0px, rgba(239, 218, 27, 0.15) 0px 10px 3px 1px;
    
    }
}

@media screen and (max-width: 749px){
    .aboutsec{
        width: 90%;
        margin: auto;
        text-align:start;
        padding: 30px;
    }
    .detail{
        font-size: 14px;
        font-weight: 700;
    }
    .headingnow{
        color: palevioletred; 
        font-size: 50px;
    }
    .gif{
        width:65%;
        margin-left: 22%;
    }
}

@media screen and (max-width: 350px){
   .detalsmallDiv{
       font-size: 20px;
    }
    .smalldiv{
        width: 60%;
    }
    #about{
        width: 80%;
        margin:auto;
        display: block;
       margin-top: 80px;
    }
   
}