.Calendermain{
        text-align: center;
    align-items: center;
    margin-top: 90px;
   margin-bottom: 50px; 
}

.snake{
    width: 50%;
    margin: auto;
}

.streaks{
    
    width: 60%;
    margin: auto;
    margin-top: 60px;
}

.stats{
    display: flex;
    width: 80%;
    margin: auto;
    justify-content: space-between;
    margin-top: 80px;
}
.mainCommits{
    width: 45%;
    box-shadow:  #fb982f 3px 10px 3px 0px; 
}
.mainlang{
    width: 45%;
    box-shadow:  #fb982f -3px 10px 3px 0px;
}

.langs{
    width: 100%;  
}
.strk{
    width:50%
}
.statsc{
    width: 100%;
}

@media screen and (max-width: 1995px){
    .snake{
        width: 50%;
        display: flex;
  justify-content: center;
  align-items: center;
        
    }
}

@media screen and (max-width: 1995px){
    .snake{
        width: 70%;
        display: flex;
        justify-content: center;
        align-items: center; 
    }
    .streaks{
        margin-top: 60px;
    }
    .strk{
    width: 100%;
    }
    .stats{
        width: 90%;
    }
}

@media screen and (max-width: 1100px){
    .snake{
        width: 70%;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }
    .langs{
        width: 100%;
    }
    .mainCommits{
        
        width: 50%;
    }
    .statsc{
        width: 100%;
    }
}

@media screen and (max-width: 859px){
    .Calendermain{
        text-align: center;
    align-items: center;
    margin-top: 40px;
   margin-bottom: 50px; 
}
}

@media screen and (max-width: 648px){
.stats{
   display: block;
   text-align: center;
  
}
.headcalender{
    font-size: 40px;
    padding: 0 20px 0 20px;
}

.mainlang{
    width: 100%;
    box-shadow:  #fb982f -3px 10px 3px 0px;
    margin-top: 70px;
}

.mainCommits{
    width: 100%;
    box-shadow:  #fb982f 3px 10px 3px 0px; 
    margin-top: 70px;
}

.langs{
    width: 100%;
}
.snake{
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center; 
}

.strk{
    width: 100%;
    }
    .streaks{
      
        width: 100%;
        margin: auto;
        margin-top: 70px;
    }
    
}