.maindivproj{
    width: 85%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    text-align: right;
    margin-bottom: 40px;
    padding: 40px;
}
#projects{
    padding: 30px 30px;
}
.projHeading{
    font-family: 'Signika Negative', sans-serif;
    color: palevioletred; 
    letter-spacing: 7px;
    font-size: 65px;
   margin-bottom: 20px;
}

.projimage{
    width: 66%;
    height: 100%;
    /* margin-top: 45px; */
}
.projimage:hover{
    width: 62%;
    transition: 2s;
    height: 100%;
    /* margin-top: 45px; */
}

.meals{
    width: 100%;
    background-color: aquamarine;
    border-radius: 30px 50px 20px 60px;
    margin: auto;
}
.projdetails{
    position: relative;
    width: 100%;
    left: -90px;
    padding: 30px;
    margin-top: 35px;
    font-size: 20px;
    background-color:var(--yellow);
    background: linear-gradient(100deg, #fbcc34 26.71%, #f78e1e 99.36%);
    border-radius: 20px 50px 20px 50px;
    padding: 30px;
}
.aboutproj{
    width: 45%;
    font-weight: 700;
    letter-spacing: 1px;
}
.techstackdiv{
  display: flex;
  justify-content: space-around;
  align-items: right;
}

.detalsmallDivproj{
    padding: 10px 40px;
    border-radius: 40px;
    box-shadow: #fdc50f 0px 1px 2px 0px, #fb982f 0px 10px 3px 1px;
}
.h1{
    font-size: 30px;
    letter-spacing: 4px;
    font-weight: 900;
}
.link{
    cursor: pointer;
}
.tech{
 font-size: 20px;
 letter-spacing: 3px;
 margin-top: 40px;
}
.headerlinks{
    align-items:center;
    gap:40px;
}
.hr{
    width: 40%;
    background-color:linear-gradient(315deg, rgba(34,193,195,1) 0%, rgba(253,251,45,1) 100%) ;
}
.Links{
    display: flex;
    gap: 30px;
    justify-content: end;
}


@media screen and  (max-width: 1995px) {
    .projdetails{
        position: relative;
