#skills{
    padding-bottom: 80px;
}
.skillsMain{
    width: 85%;
    margin: auto;
    margin-top: 150px;
    margin-bottom: 90px;
    text-align: center;
    box-shadow:  #fb982f -3px 10px 3px 0px;
}

.heading{
    font-family: 'Signika Negative', sans-serif;
    color: palevioletred; 
    letter-spacing: 7px;
    font-size: 65px;
    margin-bottom: 80px;
}

.h3tags{
    letter-spacing: 5px;
}

.frontdiv{
    margin-top: 90px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    padding: 40px;
}
.skills-card-name{
    letter-spacing: 4px;
}
.skillsHead{
 display: grid;
 grid-template-columns: repeat(5,1fr);
 gap: 30px;
}

.coomuni{
    width: 38%;
}
.allSkills{
    width: 25%; 
    border-radius: 20px;
    box-shadow: #e8b50d 0px 1px 2px 0px, #f4b56e 0px 10px 3px 1px;
    /* box-shadow: rgba(248, 229, 15, 0.05) 0px 1px 2px 0px, rgba(239, 218, 27, 0.15) 0px 10px 3px 1px; */
}
.node{
    width: 30%; 
}
.express{
    width: 80%; 
    background-color: black;  
}
.post{
    width: 28%;
}
.render{
    width: 60%;
}
.vercel{
    width: 45%;
}

@media screen and (max-width: 1148px){
    .skillsHead{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        gap: 30px; 
       }
       .allSkills{
        width: 60%;
    }
}

@media screen and (max-width: 1148px){
    .skillsHead{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 30px; 
       }
       .allSkills{
        width: 50%;
    }
    .heading{
        font-family: 'Signika Negative', sans-serif;
        color: #7fc5e6;
        letter-spacing: 7px;
        font-size: 45px;
        margin-bottom: 90px;
    }
}