#home{
    padding-top: 90px;
}
.profilemain{
     background-size: cover;
   position: sticky;
    display: flex;
    justify-content: space-around;
    width: 80%;
    margin: auto;
}
.hey{
    width:70px;  
}
.myself{
    font-size: 38px;
    margin-left: 10%;
    transition: .5s;
    letter-spacing: 5px;
    transition: .5s;
    font-family: 'Signika Negative', sans-serif;
}
.dynamic-txts{
    font-size: 65px;
    color: var(--yellow);
    font-family: 'Signika Negative', sans-serif;
}
.skilled{
   font-size: 35px; 
   margin-left: 10%;
   transition: .5s;
   letter-spacing: 3px;
   margin-top:25%;
}
.buttresu{
    margin-left: 10%;
    height: 9vh;
    font-size: 22px;
    transition: .8s;
    padding: 20px 50px;
    text-decoration: none;
    font-family: 'Signika Negative', sans-serif;
    cursor: pointer;
}
.opac{
    opacity: 1;
    color: black;
}
.mainImage{
    position: absolute;
    left: 60%;
    border-radius: 20%;
    top:10%;
    width:30%;
    transition: .5s;
    /* box-shadow: rgba(17, 32, 244, 0.401) 0px 54px 55px, rgba(196, 19, 19, 0.12) 0px -12px 30px, rgba(11, 238, 60, 0.12) 0px 4px 6px, rgba(244, 236, 14, 0.17) 0px 12px 13px, rgba(4, 237, 245, 0.09) 0px -3px 5px; */
}
@media screen and (min-width: 585px) and (max-width: 1115px){
    .mainImage{
        width:40%;  
    }
}
@media screen and (min-width: 750px) and (max-width: 1010px){
.myself{
    font-size: 40px;
}
.mainImage{
    width:35%; 
}
.skilled{
    font-size: 20px; 
    margin-left: 10%;
 }
.mainbackImage{
    left:60%;
    width:90%
}
.dynamic-txts{
    font-size: 40px;
    color: var(--yellow);
    font-family: 'Signika Negative', sans-serif;
}
}

@media screen and (min-width: 490px) and (max-width: 750px){
    .profilemain{
        justify-content: space-around;
        height:650px;
    }
    .myself{
        font-size: 38px;
    }
    .dynamic-txts{
        font-size: 49px;
        color: var(--yellow);
        font-family: 'Signika Negative', sans-serif;
    }
    .mainImage{
        width:55%; 
        left: 45%;
        /* border-radius: %; */
        top:18%;
    }
    .skilled{
        font-size: 30px; 
        margin-top: 320px;
        margin-left: 10%;
     }
    .mainbackImage{
        position: absolute;
        top: 200px;
        opacity: 0.3;
        left: 33%;
        width:65%
    }
    .buttresu{
        margin-left: 10%;
        height: 5vh;
        font-size: 20px;
        padding: 20px 40px;
        text-decoration: none;
    }
}
@media screen and (max-width: 490px){
    .profilemain{
        justify-content: space-around;
        height:520px;
    }
    .profilemain{
     flex-direction:column;
    }
    .myself{
        font-size: 30px;
        margin-top: 25px;
    }
    .mainImage{
        width:66%; 
        top: 140px;
        left: 24%;
    }
    .skilled{
        font-size: 20px; 
        margin-top: 250px;
     }
    .mainbackImage{
        position: absolute;
        top: 200px;
        opacity: 0;
        left: 5%;
        width:95%
    }
    .buttresu{
        font-size: 20px;
        padding: 20px 20px;
        text-decoration: none;
        margin-top: 30px;
    }
    .dynamic-txts{
        font-size: 38px;
        color: var(--yellow);
        
    }
    .hey{
        width:58px;
    }
}
@media screen and (max-width: 351px){
    .profilemain{
        justify-content: space-around;
        height:520px;
    }
    .profilemain{
     flex-direction:column;
    }
    .myself{
        font-size: 35px;
        margin-top: 100px;
    }
    .mainImage{
        width:210px; 
        top: 200px;
        left: 18%;
    }
    .myself{
        font-size: 30px;
    }
    .dynamic-txts{
        font-size: 40px;
        letter-spacing: 2;
    }
}